.set {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: flex-start;
    align-content: space-around;
    align-items: center;
}

.card {
    max-width: 415px;
    background-color: #fff;
    border-radius: 20px;
}

.logo {
    margin-top: 25px;
}

.title {

}

.form {
    margin: 40px 30px 70px 30px;
}

.error {
    margin: inherit;
}

.text {
    display: block;
    margin-top: 30px;
}

.button {
    background-color: rgb(48, 64, 206);
    border: none;
    border-radius: 20px;
    color: #fff;
    height: 35px;
    max-width: 265px;
    margin-top: 90px;
    font-size: 16px;
}
